.button {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  text-decoration: none;
  color: $btn-text-color;
  background-color: $btn-bckgrd-defult-color;
  border-radius: 8px;
  cursor: pointer;

  @include hover(background-color, $btn-bckgrd-hover-color);
  @include active(background-color, $btn-bckgrd-active-color);
}
